.site-list {
  list-style: none;
}

.site-list__item {
  padding-top: 28px;
  padding-bottom: 28px;
}

.site-list__item:not(:last-child) {
  @media (min-width: $desktop-width) {
    border-right: 1px solid $lighter-grey;
  }
}

.site-list__link {
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  color: $black;
  text-decoration: none;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}
